.App-header {
  background-color: #ffffff;
  padding-top: 1vh;
  height: 16vh;
  width: 100vw;
  max-height: 16vh;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-image {
  height: auto;
  width: auto;
  max-height: 15vh;
  max-width: 85vw;
}

.App-body {
  height: 80vh;
  width: 100vw;
  max-height: 80vh;
  max-width: 100vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: left;
  background-color: #ffffff;
}

.App-formcolumn {
  height: 80vh;
  width: 80vw;
  max-height: 80vh;
  max-width: 80vw;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: left;
  background-color: #ffffff;
  font-size: medium;
}

.App-stationscolumn {
  height: 80vh;
  width: 80vw;
  max-height: 80vh;
  max-width: 80vw;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: left;
  background-color: #ffffff;
  font-size: medium;
  row-gap: 10px;
}
